import angular from 'angular'

export default angular
  .module('ShopHomeControllerComponentModule', [])
  .controller(
    'ShopHomeController',
    function (
      $scope,
      Restangular,
      canvas,
      catalogCategoriesService,
      config,
      placementsService,
      tagsService,
      titleService,
      tree
    ) {
      $scope.config = config
      $scope.canvas = canvas
      $scope.tags = catalogCategoriesService.tags
      $scope.tagsService = tagsService

      $scope.logPromotedPlacement = placementsService.logPromotedPlacement

      // Fetch catalog banner content
      placementsService.getCatalogBanners().then(result => {
        $scope.bannerContent = result
      })

      // Fetch catalog collection content
      placementsService.getCatalogCollections().then(result => {
        $scope.collectionsContent = result
      })

      titleService.setTitle(tree.current.name)
    }
  )
